

html, body, #root, .App {
  width: 100%;
  height: 100%;
  background-color: lightgrey;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.App, .ProjectTitle * {
  font-size: 12px;
}

.App.live, .App.live .ProjectTitle * {
  font-size: 13px;
}

.App .live-only {
  display: none;
}

.App.live .live-only {
  display: block;
}

.download-header {
  text-align: center;
  margin: 24px;
  font-size: 18px;
}

.contact-ul {
  /*font-size: 12px;*/
  line-height: 16px
}

.contact-ul a svg, .download-header svg {
  font-size: 24px;
  margin-right: 6px;
}

/* Grid */

.cv-pdf {

  height: 792px;
  width: 612px;
  background-color: white;
  margin: 0 auto;
  padding: 80px;

}
 .cv-pdf:nth-of-type(2) {
    padding-top: 120px;
  }


.App.live .cv-pdf {
  margin-top: 20px;
  padding: 40px 40px 40px 40px;
}


 /*.App.live .cv-pdf:nth-of-type(2) {
    padding-top: 40px;
  }*/

.grid-template {
  box-sizing: border-box;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto auto;
  grid-template-areas: 'contact' 'col-one';
  grid-gap: 24px;
}

.contact {
  grid-area: contact;
  display: flex;
  justify-content: space-between;
}

.contact span {
  font-size: 10px;
  font-weight: bold;
}

.contact > p {
  margin-top: 0;
}

.col-one {
  grid-area: col-one;
  max-width: 100%;
}


.col-two {
  grid-area: col-two;
}



@media screen and (max-width: 612px) {

  .App.live .grid-template.page2 {
    grid-template-rows: auto;
    grid-template-areas: 'col-one';
  }

  .App.live .cv-pdf {
    height: auto;
    width: auto;
    margin-top: 0;
  }

  .App.live .cv-pdf:nth-of-type(2) {
    padding-top: 0px;
  }

  .App.live .cv-pdf:nth-of-type(1) {
    padding-bottom: 0px;
  }

  .App.live .live-only-large {
    display: none;
  }
}

/* end of grid */


